<template>
  <div>
    <div class="l_top">
      <el-row>
        <!-- <el-button type="primary" @click="dialogFormVisible = true" style="margin-top:20px;margin-left:50px">增加设备</el-button> -->
        <el-button
          type="primary"
          @click="newPolicyOrder"
          style="margin-top:20px;margin-left:50px"
        >添加保单</el-button>
      </el-row>
    </div>
    <el-table
      ref="multipleTable"
      :data="tableData3"
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column label="保单编号" width="120">
        <template slot-scope="scope">{{ scope.row.policyNum }}</template>
      </el-table-column>
      <el-table-column prop="member.name" label="客户"></el-table-column>
      <el-table-column prop="item.detail" label="投保项目"></el-table-column>
      <el-table-column prop="item.itemNum" label="投保项目编号"></el-table-column>
      <el-table-column prop="itemOrder.orderNum" label="订单编号" show-overflow-tooltip></el-table-column>
      <el-table-column prop="warrant" label="授权书" show-overflow-tooltip></el-table-column>
      <el-table-column label="操作" width="300">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page="page"
      :page-sizes="[10, 20, 50, 100]"
      :page-size="size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="totalElements"
    ></el-pagination>
  </div>
</template>

<script>
import qs from "qs";
export default {
  data() {
    return {
      page: 0,
      size: 10,
      totalElements:1,
      tableData3: [
        {
          id: "",
          policyNum: "",
          member: [
            {
              mid: "",
              name: ""
            }
          ],
          item: [
            {
              itemNum: "",
              detail: ""
            }
          ],
          itemorder: [
            {
              orderNum: ""
            }
          ],
          warrant: ""
        }
      ],
      multipleSelection: [],
      Authorization: ""
    };
  },

  methods: {
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleEdit(index, row) {
      this.$router.push({
        path: "/policyorder/detail",
        query: { id: row.id }
      });
    },
    newPolicyOrder() {
      this.$router.push("/policyorder/detail");
    },
    list(){
      //    列表获取
      var that = this;
      this.$get("/policyorders", {
          params: {
            page: that.page,
            size: that.size
          }
        })
        .then(function(res) {
          that.tableData3 = res.data.content;
          that.page = res.data.pageable.pageNumber + 1;
          that.totalElements = res.data.totalElements;
        })
        .catch(function(error) {});
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },

    handleDelete(index, row) {
      console.log(index, row);
      var that = this;
      const id = row.id;
      this.$axios({
        method: "delete",
        url: "/policyorders/" + id,
        // headers: { Authorization: sessionStorage.getItem("auth") }
      }).then(function(res) {
        if (res.status == 200) {
          that.$message({
            message: "删除成功",
            type: "success"
          });
        }
      });
    }
  },
  mounted() {
    this.list();
  }
};
</script>

<style>
.l_top {
  height: 80px;
}
</style>



